import React from "react"
import { graphql, StaticQuery, Link } from "gatsby"
import "./home.style.scss"

const Item = ({ post }) => (
  <div className="new-blog__blog-item">
    <Link to={post.path} dangerouslySetInnerHTML={{ __html: post.title }} />
  </div>
)

const NewBlogs = ({ data }) => (
  <div className="new-blog">
    <h3 className="new-blog__title">Blog VNREBATES</h3>
    <div>
      {data.allWordpressPost.edges.map(post => (
        <Item key={`new_blogs_${post.node.wordpress_id}`} post={post.node} />
      ))}
    </div>
  </div>
)

export default props => (
  <StaticQuery
    query={graphql`
      query {
        allWordpressPost(
          sort: { order: DESC, fields: date }
          limit: 6
          filter: {
            categories: {
              elemMatch: { slug: { eq: "kinh-nghiem-giao-dich-forex" } }
            }
          }
        ) {
          edges {
            node {
              wordpress_id
              title
              path
            }
          }
        }
      }
    `}
    render={data => <NewBlogs data={data} {...props} />}
  />
)
