import React from "react"
import { graphql, StaticQuery } from "gatsby"
import "./home.style.scss"

import BlogItem from "../common/BlogItem"
import { Row } from "react-bootstrap"

const NewPosts = ({ data }) => (
  <div className="new-blog">
    <h3 className="new-blog__title">Tin tức mới</h3>
    <div>
      {data.allWordpressPost.edges.map(post => (
        <BlogItem key={`new_post_${post.node.wordpress_id}`} post={post.node} />
      ))}
    </div>
  </div>
)

export default props => (
  <StaticQuery
    query={graphql`
      query {
        allWordpressPost(
          sort: { order: DESC, fields: date }
          limit: 5
          filter: { categories: { elemMatch: { slug: { eq: "tin-forex" } } } }
        ) {
          edges {
            node {
              wordpress_id
              title
              path
              excerpt
              date(formatString: "DD-MM-YYYY")
              featured_media {
                source_url
              }
            }
          }
        }
      }
    `}
    render={data => <NewPosts data={data} {...props} />}
  />
)
